<style scoped lang = "scss">
	.login {
		padding: 50px 0px 0px 10px;
		text-align: center;
	.line {
		padding: 10px;
		overflow: hidden;
	input {
		line-height: 24px;
		font-size: 20px;
		float: left;
		border: 0px;
		border-bottom:1px;
		width:250px
	}
	div{
		color: #f00;
		line-height: 22px;
		float: right;
	}
	}
	button {
		border:0px;
		border-radius: 6px;
		font-size: 18px;
		padding: 10px 16px;
	}
	}
</style>
<template>
	<div>
		<v-header msg="">
			<router-link slot="left" to="/"></router-link>
		</v-header>
		<form class="login" v-on:submit.prevent="submit">
			<div class="line">	
				<div v-show="btn && !form.id">id</div>
				<input type="number" placeholder="id" v-model="form.id">
			</div>
			<div class="line">
				<div v-show="btn && !form.name"></div>
				<input type="text" placeholder="" v-model="form.name">
			</div>
			<button></button>
		</form>
	</div>
</template>
<script>
    import * as type from '../../store/mutation-types'
    export default {
        data() {
			return {
				btn: false, //true  false
				form: {
					id: '',
					name: ''
				}
			}
		},
		methods: {
			submit() {
				this.btn = true
				if(!this.form.id || !this.form.name) return
				this.$store.dispatch(type.USER_SIGNIN,this.form)
				this.$router.replace({ path: '/home' })
			}
		}
    }
</script>


